<template>
  <div class="d-flex flex-column mx-auto mt-0">
    <div class="statusMessage" id="statusMessage">
    </div>
    <div class="game__container m-auto" id="gameContainer">
      <table v-for="grid in game.gridArray.length" class="grid table w-auto">
        <tr v-for="row in game.gridArray[grid].length" class="row">
          <div v-for="column in game.gridArray[grid][row].length">
            <td v-if="game.gridArray[grid][row][column] === '-'" class="col" :id="'notSet' + grid + '-' + row + '-' + column"
            >
            </td>
            <td v-else class="col" :class="'col__player' + getPlayer(grid, row, column)">
            </td>
          </div>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'GameContent',
  data: () => ({
    size: 4
  }),
  computed: {
    ...mapGetters([
      'players',
      'game'
    ])
  },
  methods: {
    ...mapActions([
        'setGame'
    ]),
    getPlayer(grid, row, column) {
      return this.game.gridArray[grid][row][column] === 'X' ? 1 : 2
    }
  },
  created() {
    axios.post('/json').then(({ data }) => {
      this.setGame(data)
    })
  }
}
</script>

